/*
    font face
 */
@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto-Medium.woff);
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto-Regular.woff);
  font-weight: 400;
}

/*
    general style
 */
* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-collapse: collapse;
}

img {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

a {
  color: inherit;
  cursor: pointer;
}

a:focus {
  outline: none;
}

button {
  cursor: pointer;
  border: none;
}

.container {
  max-width: 480px;
  margin: 0 auto;
}

body {
  font-family: "Roboto";
  color: #000000;
  font-size: 18px;
  line-height: 130%;
  font-weight: 400;
}

.overflow {
  position: relative;
  overflow: hidden;
  max-width: 480px;
  margin: 0 auto;
  outline: 1px solid #690000;
}

input,
select,
textarea,
a,
button {
  font-family: "Roboto";
}

.offer {
  background: url(../img/1_title_bg.svg) 0% 0% no-repeat;
  background-size: contain;
  padding: 20px 15px 50px 15px;
}

.offer__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-title {
  font-weight: 500;
  font-size: 38px;
  line-height: 45px;
  text-align: center;
  color: #ffffff;
  margin-bottom: 10px;
  width: 100%;
}

.main-subtitle {
  text-align: center;
  color: #ffffff;
  width: 100%;
  max-width: 260px;
  margin-bottom: 15px;
}

.sale-triangle {
  background: url(../img/1_sale_bg.svg) 0% 0% no-repeat;
  background-size: contain;
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
  text-align: center;
  color: #ffffff;
  width: 150px;
  height: 140px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 15px;
  padding-bottom: 5px;
  position: relative;
  z-index: 10;
  margin-top: 30px;
  align-self: flex-start;
}

.offer__product {
  margin-top: -220px;
  width: 100%;
}

.price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #e4e4e4;
  border-radius: 20px;
  padding: 10px;
  width: 100%;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin: 10px 0px 20px 0px;
}

.old-price {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.old-price .price-numb {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  position: relative;
}

.old-price .price-numb::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #ff4747;
  position: absolute;
  left: 0;
  top: 50%;
}

.new-price {
  background: linear-gradient(146.96deg, #b60000 3.18%, #ef5600 91.7%);
  border-radius: 20px;
  width: 50%;
  height: 110px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.new-price .price-text {
  max-width: 140px;
}

.price-text {
  margin-bottom: 5px;
}

.new-price .price-numb {
  font-weight: bold;
  font-size: 32px;
  line-height: 42px;
}

.offer__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 25px;
}

.offer__item {
  width: 32%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.offer__item img {
  margin-bottom: 10px;
}

.order-card {
  width: 100%;
  background: #e4e4e4;
  box-shadow: 0px 0px 15px rgba(191, 191, 191, 0.3);
  border-radius: 20px;
  padding: 35px 15px 30px 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.order-card__title {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.btn-order {
  width: 100%;
  height: 70px;
  max-width: 350px;
  background: linear-gradient(180deg, #14cacb 0%, #0369b4 100%);
  border-radius: 30px;
  font-weight: 500;
  font-size: 48px;
  line-height: 70px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 15px;
  user-select: none;
}

.sale-block {
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}

.sale-block span {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
  background: #ff6e1d;
  border-radius: 50%;
  padding: 4px;
}

.timer-wrapper {
  width: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 45px;
  margin-top: 15px;
  position: relative;
}

.timer_bg1 {
  position: absolute;
  left: -20px;
  top: -13px;
}

.timer_bg2 {
  position: absolute;
  left: 129px;
  top: -15px;
}

.timer_bg3 {
  position: absolute;
  left: 276px;
  top: -15px;
}

.eTimer {
  margin-left: 0px;
  font-weight: bold;
  font-weight: 500;
  line-height: 75px;
  text-transform: uppercase;
}

.eTimer .etUnit .etNumber {
  margin: 0px !important;
  font-size: 58px !important;
  width: 36px !important;
}

.eTimer .etTitle {
  margin-bottom: 0px !important;
}

.eTimer .etSep {
  padding: 0px !important;
  margin: 0px 2px;
}

.eTimer .etSep {
  display: none !important;
}

.eTimer .etDays {
  display: none !important;
}

.eTimer .etHours {
  margin-right: 80px;
  position: relative;
}

.eTimer .etHours::after {
  content: ":";
  display: block;
  position: absolute;
  font-size: 64px;
  top: -5px;
  right: -48px;
}

.eTimer .etMinutes {
  margin-right: 75px;
  position: relative;
}

.eTimer .etMinutes::after {
  content: ":";
  display: block;
  font-size: 64px;
  position: absolute;
  top: -5px;
  right: -44px;
}

.eTimer .etSeconds {
  margin-right: 0px;
  position: relative;
}

.eTimer .etHours::before {
  content: "Jam";
  display: block;
  position: absolute;
  bottom: -10px;
  right: calc(66% - 25px);
  font-size: 14px;
  line-height: 16px;
}

.eTimer .etMinutes::before {
  content: "Minit";
  display: block;
  position: absolute;
  bottom: -11px;
  right: calc(57% - 25px);
  font-size: 14px;
  line-height: 16px;
}

.eTimer .etSeconds::before {
  content: "Detik";
  display: block;
  position: absolute;
  bottom: -12px;
  right: calc(61% - 25px);
  font-size: 14px;
  line-height: 16px;
}

